.features {
  margin-bottom: 95px;

  &__title {
    margin-bottom: 50px;

    font-size: 58px;
    line-height: 58px;
    letter-spacing: 0.14em;
    color: $turquoiseColor;
    -webkit-text-stroke: 1px $turquoiseColor;
  }

  &__container {
    @include onTablet {
      display: flex;
      justify-content: space-between;
      gap: 20px;
    }
  }
}
