.preview {
  position: relative;
  margin-bottom: 100px;

  &__polygon {

    @include onTablet {
      position: absolute;
      margin-top: -124px;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: $yellowLightColor;
      clip-path: polygon(0% 0%, 80% 0%, 80% 35%, 0% 82%);
      z-index: -1;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    position: relative;

    @include onTablet {
      flex-direction: row-reverse;
      justify-content: space-around;
    }
  }

  &__image-container {
    margin-left: -30px;
    margin-right: -30px;
    @include onTablet {
      margin-left: 0;
      margin-right: 0;
    }
  }

  &__image {
    width: 100%;
    margin-bottom: 24px;

    @include onTablet {
      min-width: 300px;
      max-width: 500px;
      margin-bottom: 0;
    }
  }

  // &__info {

  // }

  &__title {
    margin-bottom: 21px;

    @include onTablet {
      margin-right: -134px;
      margin-bottom: 0;
    }
  }

  &__title-text {
    font-size: 36px;
    line-height: 36px;
    letter-spacing: 0.15em;

    -webkit-text-stroke: 0.65px $turquoiseColor;
    color: $turquoiseColor;

    @include onTablet {
      font-size: 64px;
      line-height: 64px;
    }
  }

  &__price {
    margin-bottom: 20px;

    @include onTablet {
      margin-top: -20px;
      margin-left: 240px;
      margin-bottom: 44px;
    }
  }

  &__cost {
    margin-bottom: 1px;

    font-family: "Proxima Nova Bold", sans-serif;
    font-size: 26px;
    line-height: 32px;
    font-weight: 600;
    color: #333;
  }

  &__offer {
    font-family: "Proxima Nova Semibold", sans-serif;
    font-size: 14px;
    line-height: 150%;
    color: #4f4f4f;
  }

  &__buy-button {

    margin-right: 34px;

    @include onTablet {
      margin-left: 120px;
    }
  }

  &__more-about {
    position: absolute;
    top: 90%;
    right: -75px;
    display: flex;
    align-items: center;
    gap: 11px;

    text-decoration: none;

    transform: rotate(90deg);

    @include onTablet {
      top: 85%;
      right: initial;
      left: -50px;
    }
  }

  &__more-about-text {
    font-family: "Proxima Nova Light", sans-serif;
    font-size: 12px;
    line-height: 18px;
    color: $turquoiseColor;
  }

  &__more-about-arrow {
    position: relative;
    @include arrow(44px, 1px, $turquoiseColor);
  }
}
